import React from "react";
import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";

function Cart(props) {
  let state = useSelector((state) => state.reducer);
  let dispatch = useDispatch();

  return (
    <div>
      <Table responsive="sm">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {state.map((a, i) => {
            return (
              <tr key={i}>
                <td>{a.id + 1}</td>
                <td>{a.name}</td>
                <td>{a.quan}</td>
                <td>
                  <button
                    onClick={() => {dispatch({ type: "", payload: a.id });}}
                  >
                    +
                  </button>
                  <button
                    onClick={() => {
                      dispatch({ type: "", payload: a.id });
                    }}
                  >
                    -
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
      {props.alert === true ? (
        <div className="my-alert2">
          <p>     20%</p>
          <button
            onClick={() => {
              props.dispatch({ type: "" });
            }}
          >
            
          </button>
        </div>
      ) : null}
    </div>
  );
}

// function stateprops(state) {
//   return {
//     state: state.reducer,
//     alert: state.reducer2,
//   };
// }

// export default connect(stateprops)(Cart);

export default Cart;
